import classNames from 'classnames';
import { useLayoutEffect, useState } from 'react';
import { Checkbox, message, Statistic } from 'antd';
import { Link } from 'react-router-dom';
import TitleCombo from '../../components/UI/TitleCombo/TitleCombo';
import CustomButton from '../../components/UI/CustomButton/CustomButton';
import NumberInput from '../../components/UI/NumberInput/NumberInput';
import styles from './Participate.module.scss';
import { checkTime, formatNumber } from '../../utils/utils';
import {
  useAllTokenAllowance,
  useAllTokenBalance,
  useTokenApprove,
  useZepochNodeTotalSupply,
} from '../../hooks/useToken';
import { useNameservice } from '../../store/nameservice-context';
import { CONTRACT_NAME } from '../../constants/addresses';
import { useWallet } from '../../store/wallet-context';
import { useLockZBC, usePhase1Config } from '../../hooks/useManager';
import {
  DEFAULT_REFERRAL_CODE,
  PHASE_1_NFT_AMOUNT,
  WHITELIST_END_TIMESTAMP,
  WHITELIST_START_TIMESTAMP,
} from '../../constants/misc';
import {
  useIsRegistered,
  useRegisteredInvitationCodeContains,
} from '../../hooks/useInvitationCenter';
import { useQuery } from '../../hooks/useQuery';

const { Countdown } = Statistic;

const SINGLE_TRANSACTION_NFT_LIMIT = 50;

function Stake() {
  const [amount, setAmount] = useState(1);
  const [agreementChecked, setAgreementChecked] = useState(false);
  const { account } = useWallet();
  // get phase 1 config
  const { data: phase1Config } = usePhase1Config();
  const { phase1StartTimestamp, phase1ZbcLockAmount } = phase1Config;
  const addresses = useNameservice();
  const { data: isRegistered } = useIsRegistered();
  const queryInviterCode = useQuery()?.get('referral');
  const { isValidInvitation } = useRegisteredInvitationCodeContains();
  // get zbc balance and allowance
  const { zbcBalance } = useAllTokenBalance();
  const { zbcAllowance } = useAllTokenAllowance(addresses[CONTRACT_NAME.Manager]);
  // approve zbc hook
  const { state: approveZBCState, approve: approveZbc } = useTokenApprove(
    addresses[CONTRACT_NAME.ZBC],
  );
  // lock zbc hook
  const { state: lockZBCState, lockZBC } = useLockZBC();
  // get total zepoch supply
  const { data: zepochNodeTotalSupply } = useZepochNodeTotalSupply();

  const handleApprove = () => {
    if (approveZBCState.isLoading || !addresses[CONTRACT_NAME.Manager]) {
      return;
    }
    if (!checkTime(phase1StartTimestamp * 1000)) {
      message.info('Not start yet');
      return;
    }
    if (!account) {
      message.info('Please connect to wallet');
      return;
    }
    // test for agreement checked
    if (!agreementChecked) {
      message.info('Please check the box for the risk agreement');
      return;
    }

    approveZbc(addresses[CONTRACT_NAME.Manager]);
  };

  const handleLockZBC = async () => {
    if (lockZBCState.isLoading || !Object.keys(phase1Config).length) {
      return;
    }
    if (!checkTime(phase1StartTimestamp * 1000)) {
      message.info('Not start yet');
      return;
    }
    if (!account) {
      message.info('Please connect to wallet');
      return;
    }
    // test enough zbc balance
    if (phase1ZbcLockAmount * amount > zbcBalance.data) {
      message.info('Not enough ZBC');
      return;
    }
    // test enough nft to sell in current phase
    if (amount + zepochNodeTotalSupply > PHASE_1_NFT_AMOUNT) {
      message.info('Not enough Zepoch Node left');
      return;
    }
    // prevent out of gas
    if (amount > SINGLE_TRANSACTION_NFT_LIMIT) {
      message.info(
        `A maximum of ${SINGLE_TRANSACTION_NFT_LIMIT} Nodes can be acquired in a single transaction`,
      );
      return;
    }
    // test for agreement checked
    if (!agreementChecked) {
      message.info('Please check the box for the risk agreement');
      return;
    }
    // handle invitation code
    let invitationCode = DEFAULT_REFERRAL_CODE;
    if (isRegistered) {
      invitationCode = '';
    } else if (queryInviterCode) {
      // handle invitationCode from query param
      if (!(await isValidInvitation(queryInviterCode))) {
        message.info('Invalid referral code');
        return;
      }
      invitationCode = queryInviterCode;
    }
    lockZBC(amount, invitationCode);
  };

  const onAgreementCheckedChange = (e) => {
    setAgreementChecked(e.target.checked);
  };

  const getStage1Button = () =>
    zbcAllowance.data ? (
      <CustomButton
        className={styles.BuyButton}
        rounded
        onClick={handleLockZBC}
        loading={lockZBCState.isLoading}>
        <div className={styles.buyButtonContent}>
          BUY NOW <br />
          <span>Balance:{formatNumber(zbcBalance.data)} ZBC</span>
        </div>
      </CustomButton>
    ) : (
      <CustomButton
        className={styles.BuyButton}
        rounded
        loading={approveZBCState.isLoading}
        onClick={handleApprove}>
        <div className={styles.buyButtonContent}>
          APPROVE ZBC <br />
          <span>Balance:{formatNumber(zbcBalance.data)} ZBC</span>
        </div>
      </CustomButton>
    );

  return (
    <>
      <TitleCombo>
        <h2>Get Zebec Node Today</h2>
        <p className={styles.ParticipateDesc}>
          Any $ZBC token holder is able to participate with a laptop. By buying the certificate to
          operate a Zebec node, youll share the benefit with Zebecs growing.
        </p>
      </TitleCombo>
      <div className={styles.ProgressBarWarp}>
        <div className={classNames(styles.Stage, styles.Begins)}>
          {phase1StartTimestamp && !checkTime(phase1StartTimestamp * 1000) ? (
            <div className={styles.StageCountdown}>
              Begins in <Countdown value={phase1StartTimestamp * 1000} format="H:m:ss" />
            </div>
          ) : (
            <div />
          )}
          <div className={styles.StageServings}>
            <span className={styles.StageServingsActive}>
              {formatNumber(zepochNodeTotalSupply)}
            </span>{' '}
            / {formatNumber(PHASE_1_NFT_AMOUNT)}
          </div>
        </div>
        <div className={styles.ProgressBar}>
          <div
            className={styles.ProgressBarActive}
            style={{ width: `${(zepochNodeTotalSupply * 100) / PHASE_1_NFT_AMOUNT}%` }}
          />
        </div>
      </div>

      <div className={classNames(styles.ParticipateCard, styles.ParticipateBuy)}>
        <div className={styles.Stage}>
          <div className={styles.StageTag}>
            Stage 1{' '}
            <span>
              Stake {formatNumber(phase1ZbcLockAmount, '0a')} $ZBC to get your Zepoch Nodes
            </span>
          </div>
        </div>

        <div className={styles.ParticipateBuyWarp}>
          <div className={styles.ParticipateBuyImageWarp}>
            {/* <img src="/static/images/Participate/zepochValidator.png" alt="stage1" /> */}
            <video
              autoPlay
              muted
              loop
              playsInline
              poster="/static/images/NFT/zepoch.png"
              data-aos="fade-right">
              <source src="/static/images/NFT/zepoch.mp4" type="video/mp4" />
            </video>
          </div>
          <div className={styles.ParticipateBuyForm}>
            <TitleCombo className={styles.ParticipateBuyFormTitle}>
              <h3>ZBC Zepoch Node NFT </h3>
              <p className={styles.ParticipateBuyFormDesc}>
                Zepoch Node NFT will serve as the credential of the future zebec public chain node
                validator. After the public chain development is completed in 2023, NFT holders are
                able to run the Zebec network.
              </p>
              <p
                className={styles.ParticipateBuyFormDesc}
                style={{
                  color: 'rgba(255, 255, 255, 0.5)',
                  marginTop: '10px',
                }}>
                Note: This NFT cannot be transferred or traded until the network construction is
                completed. (before all 30,000 are sold.)
              </p>
            </TitleCombo>
            <div className={styles.Price}>
              <div>
                <div className={styles.StageServings}>
                  <span className={styles.StageServingsActive}>
                    {formatNumber(zepochNodeTotalSupply)}
                  </span>{' '}
                  / {formatNumber(PHASE_1_NFT_AMOUNT)} nodes sold
                </div>
              </div>
              <div>Price: {formatNumber(phase1ZbcLockAmount)} ZBC</div>
            </div>
            <NumberInput value={amount} onChange={(v) => setAmount(v)} />
            <div className={styles.Cost}>
              <span>Cost {formatNumber(phase1ZbcLockAmount * amount)} ZBC</span>
            </div>

            <div className={styles.Protocol}>
              <Checkbox onChange={onAgreementCheckedChange}>
                I am aware of the content of the event and do so at my own risk
              </Checkbox>
              {/* <Checkbox>
                I have read, understand and agree to the
                <span className={styles.ProtocolText}>Terms of Service</span>
              </Checkbox>
              <Checkbox>
                I have read, understand and agree to the
                <span className={styles.ProtocolText}>Privacy Policy</span>
              </Checkbox>
              <Checkbox>
                I have read, understand and agree that ZBC Zepoch Nodes are
                <span className={styles.ProtocolText}>not investments</span>
              </Checkbox> */}
            </div>
            {getStage1Button()}
          </div>
        </div>

        <div className={styles.Attention}>
          <div className={styles.AttentionTitle}>
            Attention
            <img src="/static/images/Participate/questionIcon.svg" alt="Attention" />
          </div>
          <div className={styles.AttentionDesc}>
            Zec pledge in the contract, has NFT proof rights, can generate mapping when the main
            network goes online, has POS node multi-signing rights. We have the right to vote in
            community Dao, the first batch of participation opportunities and ecological
            participation whitelist.
          </div>
        </div>
      </div>

      <TitleCombo>
        <h2>Stage 1 Bonus </h2>
        <p className={styles.ParticipateDesc}>
          Themis Gavel NFT will be given to all Stage 1 Zepoth Node holders for free.
        </p>
      </TitleCombo>

      <div className={styles.ThemisGavel}>
        {/* <img src="/static/images/Participate/themisGavel.png" alt="stage1" data-aos="fade-right" /> */}
        <video
          autoPlay
          muted
          loop
          playsInline
          poster="/static/images/NFT/themis.png"
          data-aos="fade-right">
          <source src="/static/images/NFT/themis.mp4" type="video/mp4" />
        </video>

        <div className={styles.ThemisGavelInfoWarp} data-aos="fade-left">
          <TitleCombo className={styles.ThemisGavelTitle}>
            <h3>ZBC Themis Gavel </h3>
            <p className={styles.ParticipateDesc}>
              With this non-transferable Themis Gavel, holders are honoured to be one of the Zebec
              governors for a lifetime. All Themis Gavel holders will collectively make critical
              decisions about the future of the project, such as technical upgrades, treasury
              allocations, partnerships and investments.
            </p>
            <p
              className={styles.ParticipateBuyFormDesc}
              style={{
                color: 'rgba(255, 255, 255, 0.5)',
                marginTop: '10px',
              }}>
              Note: This NFT cannot be transferred or traded until the network construction is
              completed. (before all 30,000 are sold.)
            </p>
          </TitleCombo>

          <div className={classNames(styles.Attention2, styles.BlueText, styles.BlueBg)}>
            <div className={styles.AttentionTitle}>How to get ZBC Themis Gavel NFT</div>
            <div className={styles.AttentionDesc}>
              <div>
                1.Participating in the 1st stage of Operation Horizon, the first 288 Jehovah Node
                holders will automatically be given a Themis Gavel for free.
              </div>
              <div>
                2.Participating in the 2nd stage of Operation Horizon and getting the Jehovah Node,
                then there will be a few community promotion tasks that need to be completed, once
                we verify all tasks are done, Themis Gavel will automatically be given.
              </div>
            </div>
          </div>
        </div>
      </div>
      <div className={styles.Invite}>
        <div className={styles.InviteContentWarp}>
          <div className={styles.InviteLeft}>
            <img
              className={styles.InviteLogo}
              src="/static/images/Participate/logo.svg"
              alt="zebec"
            />
            <div className={styles.InviteTitle}>
              <div className={styles.InviteTitleText}>Points Giveaway</div>
              <div className={styles.Rrewards}>
                <img
                  className={styles.InviteLogo}
                  src="/static/images/Participate/pointIcon.svg"
                  alt="zebec"
                />
                5% Referral Rrewards
              </div>
            </div>
            <div className={styles.InviteDesc}>
              each successful referred purchase of Jehovah NFT will be rewarded 1000 points
            </div>
          </div>
          <div className={styles.InviteRight}>
            <Link to="/profile">
              <CustomButton className={styles.buyButton} rounded>
                <span className={styles.buyButtonText}>Invite now</span>
              </CustomButton>
            </Link>

            <img
              className={styles.InviteAwesome}
              src="/static/images/Participate/awesome.png"
              alt="zebec"
            />
          </div>
        </div>
        <img
          className={styles.InviteTextBg}
          src="/static/images/Participate/jehovahNodeonSale.svg"
          alt="jehovahNodeonSale"
        />
      </div>
    </>
  );
}

function Coming() {
  const whitelistStarted = checkTime(WHITELIST_START_TIMESTAMP * 1000);

  return (
    <div className={styles.Coming}>
      <TitleCombo className={styles.ComingWarp}>
        <h2>Zebec Operation Horizon Whitelist</h2>
        {whitelistStarted ? (
          <>
            <div className={styles.CountDown}>
              <span>Ends in:</span>
              <Countdown value={WHITELIST_END_TIMESTAMP * 1000} format="D:H:m:s" />
            </div>
            <a
              href="https://notionforms.io/forms/whitelist-1"
              target="_blank"
              rel="noopener noreferrer">
              <CustomButton rounded>Apply for Whitelist</CustomButton>
            </a>
          </>
        ) : (
          <>
            <div className={styles.CountDown}>
              <span>Opens in:</span>
              <Countdown value={WHITELIST_START_TIMESTAMP * 1000} format="D:H:m:s" />
            </div>
            <CustomButton
              rounded
              disabled
              onClick={() => {
                message.info('Open Soon');
              }}>
              Apply for Whitelist
            </CustomButton>
          </>
        )}
      </TitleCombo>
      <img src="/static/images/Participate/countdownBg.png" alt="jehovahNodeonSale" />
    </div>
  );
}

const Participate = () => {
  const whiteListEnded = checkTime(WHITELIST_END_TIMESTAMP * 1000);
  useLayoutEffect(() => {
    window.scrollTo(0, 0);
  }, []);
  return (
    <div className={styles.Participate} data-aos="fade-up">
      {whiteListEnded ? <Stake /> : <Coming />}
    </div>
  );
};

export default Participate;
