import React, { useEffect, useRef } from 'react';
import PropTypes from 'prop-types';
import classNames from 'classnames';
import { Col, Row } from 'antd';

import { SUPPORTTED_WALLET, WALLET_INFO } from '../../constants/wallets';

import styles from './WalletMenu.module.scss';
import { useWallet } from '../../store/wallet-context';

const WalletMenu = (props) => {
  const { className } = props;
  const { walletMenuVisible, toggleWalletMenuVisible, connect } = useWallet();
  const menuRef = useRef();
  const clickCount = useRef(0);

  const handleConnectWallet = (walletType) => {
    toggleWalletMenuVisible();
    connect(walletType);
  };

  useEffect(() => {
    const handleClick = (event) => {
      if (!!clickCount.current && !menuRef.current.contains(event.target)) {
        toggleWalletMenuVisible();
      }
      clickCount.current += 1;
    };

    if (walletMenuVisible) {
      window.addEventListener('click', handleClick);
    } else {
      window.removeEventListener('click', handleClick);
      clickCount.current = 0;
    }

    return () => {
      window.removeEventListener('click', handleClick);
    };
  }, [walletMenuVisible, toggleWalletMenuVisible]);
  return (
    <div
      className={classNames(styles.WalletMenu, className, { [styles.visible]: walletMenuVisible })}
      ref={menuRef}>
      <h2 className={styles.title}>Select a wallet to continue</h2>
      <Row gutter={[16, 16]}>
        {SUPPORTTED_WALLET.map((item) => (
          <Col span={12} key={item}>
            <div
              className={styles.MenuItem}
              onClick={() => {
                handleConnectWallet(item);
              }}>
              <img src={`/static/images/Wallets/${item}.png`} alt={item} />
              <h2>{WALLET_INFO[item].name}</h2>
            </div>
          </Col>
        ))}
      </Row>
    </div>
  );
};

WalletMenu.propTypes = {
  className: PropTypes.string,
};

WalletMenu.defaultProps = {
  className: '',
};

export default WalletMenu;
