import { useEffect, useState } from 'react';
import { Redirect, Route, Switch } from 'react-router-dom';
import AOS from 'aos';

import WalletMenu from './components/WalletMenu/WalletMenu';
import { useWallet } from './store/wallet-context';
import Header from './components/Header/Header';
import Footer from './components/Footer/Footer';
import { PAGES } from './constants/pages';

import 'aos/dist/aos.css';
import 'antd/dist/antd.css';
import './App.css';

const CONCRETE_PAGES = PAGES.filter((item) => !!item.component);

function App() {
  const { account } = useWallet();
  const [pages, setPages] = useState(CONCRETE_PAGES.filter((item) => !item.protected));

  useEffect(() => {
    setPages(CONCRETE_PAGES.filter((item) => !item.protected || account));
  }, [account]);

  useEffect(() => {
    AOS.init({
      duration: 800,
      once: true,
    });
  }, []);

  return (
    <div className="App">
      <Header />
      <Switch>
        {pages.map((item) => {
          const PageComponent = item.component;
          return (
            <Route path={item.path} exact={!!item.component} key={item.id}>
              <PageComponent />
            </Route>
          );
        })}
        <Redirect to="/about" />
      </Switch>
      <Footer />
      <WalletMenu />
    </div>
  );
}

export default App;
