export const useLockZBC = () => {
  const managerContract = useManagerContract();
  const { account } = useWallet();

  const { state, write } = useContract(managerContract, 'lockZBC');

  const lockZBC = (amount, invitationCode) => {
    if (!(managerContract && amount && account)) {
      return;
    }
    write([amount, ethers.utils.formatBytes32String(invitationCode)]);
  };

  return { state, lockZBC };
};

export const useZPointStatistic = (accountAddress = null) => {
  const decodeZPointStatistic = useCallback(
    (config) => ({
      zPointPurchasing: Number(ethers.utils.formatEther(config.zPointPurchasing)),
      zPointReferral: Number(ethers.utils.formatEther(config.zPointReferral)),
      zPointStaking: Number(ethers.utils.formatEther(config.zPointStaking)),
      zPointBonus: Number(ethers.utils.formatEther(config.zPointBonus)),
      zPointGain: Number(ethers.utils.formatEther(config.zPointGain)),
      commissionUsdt: Number(ethers.utils.formatEther(config.commissionUsdt)),
      commissionBusd: Number(ethers.utils.formatEther(config.commissionBusd)),
    }),
    [],
  );
  const managerContract = useManagerContract();
  const { updateByTimer } = useUpdate();
  const { state, read } = useContract(
    managerContract,
    'zPointStatistic',
    decodeZPointStatistic,
    {},
  );
  const { account } = useWallet();
  useEffect(() => {
    const fetchData = () => {
      if (!(managerContract && (ethers.utils.isAddress(accountAddress) || account))) {
        return;
      }
      read([accountAddress || account]);
    };
    fetchData();
  }, [updateByTimer, read, managerContract, account, accountAddress]);
  return state;
};

